body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eadbcb;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/*tlačítko zaměstnanci, tlačítko task=úkoly*/
#bntemp, #bnttas {
  background-color: #c6ac8f;
  margin: 30px;
  width: 200px;
  height: 50px;
  color: white;
  font-size: 20px;
}

/* tlačítko přidání zaměstnance */
#btnaddempl {
  background-color: black;
  margin: 30px;
  width: 200px;
  height: 40px;
  color: linen;
  font-size: 17px;
}

/* jméno a příjmení zaměstnance */
#jmaprnz {
  width: 210px;
  height: 30px;
  background-color: #c9ada7;
}

/* výpis zaměstnanců */
#vypzam {
  width: 500px;
  color: black;
  background-color: #c9ada7;
  font-weight: bolder;
  margin: auto;
}

#vypzam :nth-child(even) {
  font-weight: bolder;
  background-color: black;
  color: #d0ae86;
}

/* tlačítko odebrání zaměstnance */
#btmodebzam {
  border-color: none;
  background-color: transparent;
  outline: none;
  outline-width: 0px;
  border-width: 0px;
  color: red;
  font-weight: bolder;
  cursor: pointer;
}

h1 {
  color: #9a6e6e;
}

#zenymuzi{
  color: #9a6e6e;
  font-weight: bolder;
  font-size: 1.3em;
}

#pocetmetru, #pocethodin {
  background-color: #c9ada7;
  margin-bottom: 10px;
  width: 170px;
  height: 25px;
}

#plantlacitko {
  width: 170px;
  height: 25px;
}

